import { Link } from "react-router-dom";

const BurgerMenu = ({active, toggleActive}) => {
    return (
    <ul className={active ? 'header-menu bg-slate-100 visible transition opacity-100 h-full fixed left-0 z-50 w-1/6 flex justify-center flex-col text-center' : 'header-menu bg-slate-100 invisible transition opacity-0 h-full fixed left-0 z-50 w-1/6 flex justify-center flex-col text-center'}>
            <Link to='/'>
                <li onClick={toggleActive} className="header-menu__li cursor-pointer"></li>
            </Link>
            <li onClick={toggleActive} className="header-menu__li cursor-pointer"> </li>
            <li onClick={toggleActive} className="header-menu__li cursor-pointer"></li>
            <li onClick={toggleActive} className="header-menu__li cursor-pointer"> </li>
            <li onClick={toggleActive} className="header-menu__li cursor-pointer"></li>
    </ul>
    )
}

export default BurgerMenu;