import logo from '../img/logo.png'

const Footer = () => {
    return (
        <footer className="footer min-h-28 bg-slate-100">
            <div className="footer-container py-4">
                <img src={logo} alt="" className="footer-img mx-auto w-44"/>
                <h1 className="footer-title text-center font-semibold pt-4">          -</h1>
            </div>
        </footer>
    )
}

export default Footer;