import { HashRouter as Router, Route, Routes } from 'react-router-dom';

import Header from "./components/Header";
import Footer from './components/Footer';

import Main from './pages/Home'
import Assortment from './pages/Store';
import BurgerMenu from './other/BurgerMenu';
import ItemCardDetail from './pages/ItemCardDetail';

import { useState } from 'react';

const App = () => {

  const [active, setActive] = useState(false);
  
  const toggleActive = () => {
    setActive(!active);
  }

  return (
    <>
    <Router>
      <BurgerMenu active={active} toggleActive={toggleActive}/>
      <Header active={active} setActive={setActive} toggleActive={toggleActive}/>

      <Routes>
          <Route path='/' element={<Main/>}></Route>
          <Route path='/assortment' element={<Assortment/>}></Route>
          <Route path='/:itemDetailsId' element={<ItemCardDetail/>}></Route>
      </Routes>
    </Router>

    <Footer/>
    </>
  );
}

export default App;