import axios from 'axios';

class GetApiFromJson {
    
    getApi = async () => { //   async/await  then  catch,     
        try {
            const response = await axios('./catalog.json'); //        github
            return response.data;
        } catch(error) {
            console.error('   :', error);
            throw error; //  ,     
        }
    }
}

export default GetApiFromJson;